    "question": "What do bispecific CD38 antibodies bind to and how do they treat myeloma?",
    "model": "paperqa-1.1.1",
    "date": "2023-04-11",
    "answer": "Bispecific CD38 antibodies are a promising immunotherapy for the treatment of multiple myeloma. These antibodies work by redirecting T cells to attack CD38-expressing myeloma cells (Shih2022). CD38 is a promising target for immunotherapy in multiple myeloma due to its high expression on myeloma cells and its role in regulating the immune system (Shih2022). Specific examples of CD38-targeting bispecific antibodies mentioned in the literature include BI38-3, MEDI2228, and AMG 424 (Shih2022). However, neither Shih2022 nor Verkleij4076 provide a direct quote that specifically answers the question of what bispecific CD38 antibodies bind to and how they treat myeloma. \n\nThat being said, other sources suggest that CD38-targeting antibodies, such as daratumumab or isatuximab, are used in combination therapy and can be effective in eliminating myeloma cells (Verkleij4076). These antibodies bind to CD38 on the surface of myeloma cells, leading to antibody-dependent cellular cytotoxicity (ADCC) and complement-dependent cytotoxicity (CDC) (Palumbo2016). ADCC involves the binding of the antibody to the target cell, which then activates immune cells to attack and kill the target cell. CDC involves the activation of the complement system, which leads to the formation of a membrane attack complex that can lyse the target cell (Palumbo2016). \n\nIn summary, while the literature suggests that bispecific CD38 antibodies are a promising immunotherapy for the treatment of multiple myeloma, the specific mechanism of action of these antibodies is not provided in the sources available. However, other CD38-targeting antibodies, such as daratumumab or isatuximab, are known to bind to CD38 on the surface of myeloma cells and induce ADCC and CDC, leading to the elimination of myeloma cells (Verkleij4076, Palumbo2016). \n\nSources: \n- Shih2022 pages 14-15\n- Verkleij4076 pages 1-1\n- Palumbo2016",
