    "question": "What therapeutics are available or being researched for ALS as of 2022?",
    "model": "paperqa-1.1.1",
    "date": "2023-04-11",
    "answer": "As of 2022, several antisense therapeutics are being researched for ALS, including BIIB105, an antisense therapeutic designed to decrease ATXN2 expression, which has demonstrated increased survival in a mouse model of TDP-43 ALS and is currently undergoing phase I clinical evaluation (Roberts2022 pages 11-12). Additionally, an investigational FUS-targeted AO (ION363) has been administered to one patient, and a phase I-III clinical trial for this drug has recently been announced (Roberts2022 pages 11-12). However, the C9orf72 targeting AO BIIB078 was recently evaluated in phase I clinical trial and designed to selectively target C9orf72 transcripts with repeat expansions, but the trial was terminated in March 2022 due to not meeting primary or secondary endpoints (Roberts2022 pages 11-12). Alternative AO strategies and sequences targeting C9orf72 are currently undergoing clinical evaluation, including WVE-004 and a\ufb01nersen (Roberts2022 pages 11-12). Tofersen (BIIB067) is an antisense therapy designed to target and reduce SOD1 mRNA, currently under evaluation in phase III studies for patients carrying pathogenic SOD1 mutations (Roberts2022 pages 11-12).\n\nIn addition to antisense therapeutics, other therapeutic options for ALS have historically focused on slowing disease progression and providing symptomatic treatments to improve patient quality of life, including pain management, therapeutic exercise, and physiotherapy (Roberts2022 pages 9-10). Riluzole and edaravone are the only two drugs approved by the FDA for ALS treatment, with riluzole being the most widely used (Roberts2022 pages 9-10). However, their efficacy remains controversial, and more than 70 randomized drug trials in ALS patients have shown little beneficial effect on patient survival (Roberts2022 pages 9-10).\n\nWhile the article by Agrawal2022 does not provide information on available or being researched therapeutics for ALS in 2022, the article by Belgin2022 discusses past and future therapeutic strategies for the treatment of ALS, including pharmacological and non-pharmacological approaches, as well as emerging therapies such as gene therapy and stem cell therapy (Belgin2022 pages 1-1). The authors emphasize the multifaceted nature of ALS affected by many pathological mechanisms and the need for further research to develop effective treatments for ALS (Belgin2022 pages 1-1).\n\nThe article by Tereza2020 discusses various therapeutic approaches for ALS, including gene therapy, cell-based therapies, and neurotrophic factors (Tereza2020 pages 25-26). One gene therapy, VM202, has been shown to be safe and effective in reducing ALSFRS-R decline (Tereza2020 pages 25-26). Another compound, SynCav1, has shown promise in improving motor function and survival in preclinical ALS mouse models (Tereza2020 pages 25-26). Cell-based therapies, such as astrocyte-based therapies and the transplantation of human glial restricted progenitor cells, have also shown promise in preclinical and clinical trials (Tereza2020 pages 25-26). Kadimastem Ltd. has reported promising interim results in a Phase 1/2a clinical trial using AstroRx\u00ae cells, which contain functional healthy astrocytes derived from human embryonic stem cells (Tereza2020 pages 25-26). A Phase 1/2a open-label study will also be initiated in 2020 to investigate the safety of the transplantation of human glial restricted progenitor cells into the spinal cord of ALS patients (Tereza2020 pages 25-26).",
