    "question": "Why do birds flock?",
    "model": "paperqa-1.1.1",
    "date": "2023-04-11",
    "answer": "Birds flock for a variety of reasons, including social, navigational, and anti-predator implications (Usherwood2011). Flying in a flock can result in aerodynamic benefits, reducing power requirements, as demonstrated by a reduction in heart rate and wingbeat frequency in pelicans flying in a V-formation (Usherwood2011). However, the aerodynamic consequences of flying in the more usual \u2018cluster\u2019 flock are not well understood (Usherwood2011). The main goal of the interaction among birds in a flock is to maintain cohesion of the aggregation, which is a strong biological requirement shaped by the evolutionary pressure for survival (Ballerini1232). Stragglers and small groups are significantly more prone to predation than animals belonging to large and highly cohesive aggregations (Ballerini1232). The vast majority of models, both developed by physicists and biologists, adopt a definition of \u2018distance\u2019 that is the same as in physics, i.e. metric distance, and animals can estimate metric distance in various ways, including stereovision, retinal image size, and optic flow (Ballerini1232). The variability of shape in bird flocks increases when there are larger local differences in movement behavior in the flock, which happens when flock size is larger, interacting partners are fewer, the flock turnings are stronger, and individuals roll into the turn (Hemelrijk2011). A major benefit of bird flocking behavior is that it increases collective vigilance and allows for early detection of attacking predators, which relies on rapid transfer of information about danger from the individual first detecting the predator (Mae4173). Acoustic signals or cues of danger are likely to be particularly effective, as information is passed rapidly to all nearby members of a group, and can be detected even if a bird is not currently vigilant or is out of sight (Mae4173). Flocks self-organize to the maximum density at which a typical individual is still just able to see out of the flock in many directions, which provides for faster information transfer and hence rapid flock dynamics, an advantage over local models (Pearce1042). It optimizes the information available to each bird while maintaining the protection of a dense, coherent flock (Pearce1042).",
