    "question": "What therapeutics are available or being researched for ALS as of 2022?",
    "model": "paperqa-2.1.0",
    "date": "2023-06-05",
    "answer": "As of 2022, there is no cure for ALS, and therapeutic options aim to slow the progression of the disease and provide symptomatic relief (Roberts2022SynucleinopathyIA pages 9-10). Riluzole and edaravone are the only two drugs approved by the FDA for ALS, with riluzole being the most widely used (Roberts2022SynucleinopathyIA pages 9-10). Pain management is crucial, and non-steroidal anti-inflammatory drugs, opioids, and occasionally medicinal cannabis are used for severe pain (Roberts2022SynucleinopathyIA pages 9-10). Therapeutic exercise and physiotherapy are recommended to slow symptomatic progression and alleviate pain (Roberts2022SynucleinopathyIA pages 9-10). In the later stages of the disease, interventions to manage weight loss and malnutrition are fundamental (Roberts2022SynucleinopathyIA pages 9-10).\n\nSeveral therapeutics are being researched for ALS, including antisense oligonucleotide (AO) therapies (Roberts2022SynucleinopathyIA pages 12-12). Ionis Pharmaceuticals has developed ASOs against three targets for ALS: BIIB078 for C9ORF72, jacifusen for FUS, and tofersen for SOD1 (Holm2022ClinicalAO pages 8-8). Tofersen, an investigational ASO targeting the gene encoding SOD1, is currently in a phase III trial (Holm2022ClinicalAO pages 8-8). However, results from a 28-week initial study with 108 enrolled patients living with SOD1-ALS receiving either tofersen or placebo failed to meet the primary endpoint of change in the Revised Amyotrophic Lateral Sclerosis Functional Rating Scale (Holm2022ClinicalAO pages 8-8).\n\nOther emerging therapeutic modalities for ALS include gene therapies, viral vector deliveries, and targeting TAR DNA-binding protein of 43 kDa (TDP-43) (Roberts2022SynucleinopathyIA pages 22-23; Maragakis2022TheEL pages 2-3). The development of new preclinical models and systems based on human cells derived from actual patients has expanded the possibilities of candidate cellular targets for new drugs (Maragakis2022TheEL pages 1-1). Targeting motor neuron cell death and nuclear pore dysfunction as a core mechanism for ALS may become an important target for effective interventions (Maragakis2022TheEL pages 1-1).\n\nIn summary, while there is no cure for ALS, riluzole and edaravone are FDA-approved drugs for the disease, and various therapeutics, such as AO therapies and gene therapies, are being researched to develop more effective treatments.",
    "sources": [
      {
        "key": "1",
        "text": "https://www.tandfonline.com/doi/full/10.1080/15476286.2022.2066334"
      },
      {
        "key": "2",
        "text": "https://www.mdpi.com/1422-0067/23/16/9364"
      },
      {
        "key": "3",
        "text": "https://link.springer.com/article/10.1007/s13311-022-01307-7"
      }
    ]
