    "question": "Are SELFIEs or SMILES a better representation of molecules for deep learning?",
    "model": "paperqa-2.1.0",
    "date": "2023-06-05",
    "answer": "Based on the provided context, it is evident that both SELFIES and SMILES have been used in deep learning molecular representation. In the study by Rajan2020DECIMERTD (pages 5-7), it was found that DeepSMILES outperformed SMILES, and SELFIES further outperformed DeepSMILES in terms of average Tanimoto similarity and Tanimoto 1.0 percentage. However, the authors mentioned that the indication of SELFIES outperforming DeepSMILES is \"preliminary,\" and further research is needed to confirm this finding.\n\nThe other sources provided do not offer a direct comparison between the performance and applicability of SELFIES and SMILES in deep learning for molecular representation (Rajan2020DECIMERTD pages 3-4, Zhu2021SteppingBT pages 12-12, Rajan2020DECIMERTD pages 9-9, Zhu2021SteppingBT pages 9-10).\n\nIn conclusion, while preliminary results suggest that SELFIES may outperform SMILES in deep learning molecular representation, more research is needed to determine the definitive advantages and disadvantages of using each representation system.",
