    "question": "What therapeutics are available or being researched for ALS as of 2022?",
    "model": "perplexity",
    "date": "2023-04-09",
    "answer": "As of 2022, there are six FDA-approved drugs for treating ALS and its symptoms, including RELYVRIO, Radicava, Rilutek, Tiglutik, Exservan, and Nuedexta[1]. There are also several therapies being researched for ALS treatment. For example, an experimental drug is being tested in a global phase 3 clinical trial based on promising results from a study at Columbia University[2]. Tofersen is another therapy being investigated for treating ALS caused by mutations in the SOD1 gene[3]. Additionally, NU-9 is a potential ALS treatment that has shown to be more effective than existing FDA-approved drugs when given in combination with riluzole and edaravone[4]. The ALS Association is currently funding 121 active research projects in 12 countries with $44 million committed to research[5].",
