    "question": "Are SELFIEs or SMILES a better representation of molecules for deep learning?",
    "model": "perplexity",
    "date": "2023-04-10",
    "answer": "Based on the provided search results, selfies and smiles are both used as molecular representations for deep learning. SMILES is a popular string-based representation that has been used in computational chemistry for decades[1][2]. SELFIES is a newer representation that has been shown to be more diverse and robust than SMILES[3][1][4]. Geometric representations are also becoming more important in molecular deep learning[5]. Therefore, the choice between selfies or smiles as a better representation of molecules for deep learning may depend on the specific application and the type of information that needs to be extracted from the molecular structure.\n",
