    "question": "What materials and energy are required input to use microorganisms for synthesis of plastics?",
    "model": "scite",
    "date": "2023-04-10",
    "answer": "Microbial synthesis of plastics from renewable resources has attracted considerable attention in recent years [1]. Polyesters such as polyethylene terephthalate (PET) are amenable to biological degradation due to the action of microbial polyester hydrolases [2]. The hydrolysis products obtained from PET can be used for the synthesis of novel PET as well as become a potential carbon source for microorganisms [2]. Microorganisms and biomass can also be used for the synthesis of the constituent monomers of PET from renewable sources [2]. The use of microalgal biomass as a feedstock for PHA synthesis presents a low-cost solution [3]. Activated sludge processes can be used for treating wastewater and producing PHAs [4]. The energy required for microbial synthesis of plastics can be generated from renewable resources [1]. Therefore, the materials required input to use microorganisms for synthesis of plastics include renewable carbohydrate sources, microorganisms, biomass, and energy from renewable resources.",
