    "question": "What therapeutics are available or being researched for ALS as of 2022?",
    "model": "perplexity",
    "date": "2023-06-05",
    "answer": "As of 2022, there are several therapeutics available or being researched for ALS. Here are some of the key ones from the search results:\nFDA-Approved Drugs\nRiluzole: This drug has been approved by the FDA to treat ALS and can slow the progression of the disease by a few months\n1\n.\nEdaravone: This drug has also been approved by the FDA to treat ALS and can prolong survival by a few months\n1\n.\nRELYVRIO: This is a combination of two drugs, sodium phenylbutyrate and taurursodiol, which act to prevent nerve cell death by blocking stress signals in cells. The FDA approved RELYVRIO for use to treat ALS in 2022\n2\n.\nDrugs Being Trialed\nAMX0035: This is a combination of two drugs, sodium phenylbutyrate and taurursodiol, which act to prevent nerve cell death by blocking stress signals in cells. It has shown potent therapeutic effects in clinical trials and has been approved by the FDA for the treatment of ALS\n3\n.\nMethylcobalamin: This drug has shown promise in clinical trials for treating ALS\n3\n.\nMasitinib: This drug has shown promise in clinical trials for treating ALS\n3\n.\nCNM-Au8: This drug has shown promise in clinical trials for treating ALS\n3\n.\nTofersen: This drug has shown promise in clinical trials for treating ALS\n3\n.\nOther Therapies\nStem cell therapy: Stem cell therapy is being investigated as a potential treatment option for ALS\n4\n5\n.\nOccupational therapy: Occupational therapy can help manage symptoms and improve the quality of life for those with ALS\n4\n.\nGene therapy: Gene therapy is being investigated as a potential treatment option for ALS\n4\n.\nSmall molecule therapy: Recent studies have focused on identifying small molecules that can promote the survival of motor neurons and reduce inflammation\n4\n.\nImmunomodulation therapy: Studies have shown that inflammation plays a vital role in the progression of ALS. Some recent studies have focused on the use of immunomodulation therapy to reduce inflammation and improve the survival of motor neurons\n4\n.\nArimoclomol: This drug is currently undergoing clinical trials and is being investigated as a potential treatment option for ALS\n6\n.\nInsulin-like growth factor-1 (IGF-1): This drug is currently undergoing clinical trials and is being investigated as a potential treatment option for ALS\n5\n6\n.\nRAPA-501 autologous T cells: This therapy is currently undergoing clinical trials and is being investigated as a potential treatment option for ALS\n5\n6\n.\nIt's important to note that while some of these therapies have shown promise in clinical trials, they are still being investigated and have not yet been approved by the FDA for the treatment of ALS.",
