    "question": "What therapeutics are available or being researched for ALS as of 2022?",
    "model": "bing",
    "date": "2023-06-05",
    "answer": "According to the current web page context, there are currently seven drugs approved by the U.S. Food and Drug Administration (FDA) to treat ALS and its symptoms: Qalsody, RELYVRIO, Radicava, Rilutek, Tiglutik, Exservan and Nuedexta\u00b9. RELYVRIO is a combination of two drugs, sodium phenylbutyrate and taurursodiol, which act to prevent nerve cell death by blocking stress signals in cells. The FDA approved RELYVRIO for use to treat ALS in 2022\u00b9. Radicava\u2122 (edaravone) was approved in 2017, making it the first new treatment specifically for ALS in 22 years. An oral formulation was approved in 2022\u00b9. Rilutek (riluzole) was the first FDA-approved drug available to treat ALS \u2014 in 1995. It inhibits glutamate release and prolongs life approximately three months\u00b9. ",
