    "model": "scite",
    "date": "2023-06-05",
    "answer": "As of 2022, there is no effective therapeutic for ALS [4]. However, there are several drugs that are being researched for ALS treatment. Riluzole, a glutamatergic neurotransmission inhibitor, is the only drug approved by the USA Food and Drug Administration for ALS treatment with modest benefits on survival [1]. Recently, edaravone and AMX0035 have also been approved by the FDA for the treatment of ALS [5]. Other drugs in clinical development for ALS include AT-1501, CNM-Au8, Engensis, Pridopidine, Verdiperstat, and Zilucoplan [3]. In addition to drug therapies, emerging ALS therapies in pre-clinical or early clinical development include gene therapy, immunotherapy, and strategies that target neuroinflammation, phase separation, and protein clearance [2]. However, recent clinical trials have shown that a reappraisal of strategies to identify therapeutic targets for ALS is required [6].",
