    "answer": "To use microorganisms for the synthesis of plastics, various materials and energy inputs are required. Microorganisms and biomass can be used for the synthesis of the constituent monomers of PET from renewable sources [2]. Plastic mixtures containing various types of plastics, including fossil-based recalcitrant plastics and bioplastics, can be used as feedstock for microbial transformations [3]. The hydrolysis products obtained from PET can be used for the synthesis of novel PET as well as becoming a potential carbon source for microorganisms [4]. In addition, microbially produced polymers, which are synthesized from renewable carbon sources such as sugars and oils, are of interest for solving environment and resource problems [6]. The energy required for production and processing of bioplastics based on renewable resources is decisive, and the feedstock for generation of the overall energy is also required [1]. The ratio of nitrogen is determined largely by the maintenance requirements of microbes and the breakdown of microbial materials, which result in the recycling of microbial nitrogen [5]. Finally, to provide competitive alternatives to classical plastics, production of PHA biopolyesters has to become more economical [7].",
