    "date": "2023-06-06",
    "answer": "As of 2022, there are several therapeutics available and being researched for ALS. [2] Rilutek, Radicava, Tiglutik, Exservan, Nuedexta, and Relivrio are all FDA-approved therapies for treating ALS. Additionally, there are five emerging therapies, namely NurOwn, Tofersen, Aldesleukin, Verdepistat, and AMX0035, that are currently driving innovation in ALS research. AMX0035 has recently been approved for market authorization in the US and Canada for treating ALS. [1] The Clinical Trials Awards program by the ALS Association also supports interventional clinical trials to discover new treatments and cures for ALS, with eligible therapeutic approaches including small molecule, genetic, stem-cell, peptides, antibodies, antisense oligonucleotides, and others.",
