    "question": "We need examples of specific, molecularly-defined populations of neurons in the brain we could target in order to enhance brain function. Please suggest three well-defined populations, list the function they are linked to, list the hypothesized effects of stimulating or inhibiting them, and list the specific molecules that are most commonly used to define them.",
