    "question": "What therapeutics are available or being researched for ALS as of 2022?",
    "model": "perplexity",
    "date": "2023-06-08",
    "answer": "Amyotrophic lateral sclerosis (ALS) is a progressive neurodegenerative disorder that primarily affects motor neurons in the brain and spinal cord. There are only two FDA-approved drugs for treatment, riluzole and edaravone, which only prolong survival by a few months[1]. However, there are many novel experimental drugs in development[1][2][3][4].\n\nFDA-Approved Drugs\n- Riluzole: The first FDA-approved drug for ALS, which has been shown to modestly extend survival[5].\n- Edaravone: Approved by the FDA in 2017 for its demonstrated effect on ALS functionality[5].\n- Relyvrio: A combination of two drugs, sodium phenylbutyrate and taurursodiol, which act to prevent nerve cell death by blocking stress signals in cells. The FDA approved Relyvrio for use to treat ALS in 2022[5].\n\nExperimental Drugs\nThere are many novel experimental drugs in development, which can be classified into eight mechanistic groups[1][3]:\n- Anti-apoptotic\n- Anti-inflammatory\n- Anti-excitotoxicity\n- Regulated integrated stress response\n- Neurotrophic factors and neuroprotection\n- Anti-aggregation\n- Gene therapy\n- Immunotherapy\n\nClinical Trials\nThere are many clinical trials currently underway to test new treatments for ALS[1][2][6][7][8][9]. For example, the HEALEY platform study is highly anticipated for its data on ALS drug trial results[2]. Additionally, The Lawrence and Isabel Barnett Drug Development Program supports preclinical drug discovery and development of new or repurposed treatments for ALS[10][11]. \n\nResearch\nScientists are discovering new molecular clues to how ALS develops[12]. Advances have been made in ALS preclinical models, genetics, pathology, biomarkers, imaging, and clinical readouts over the last 10-15 years. Novel therapeutic paradigms are being applied in areas of high unmet medical need, including neurodegenerative disorders[4]. \n\nConclusion\nWhile there are only two FDA-approved drugs for ALS, there are many novel experimental drugs in development, and clinical trials are currently underway to test new treatments. Scientists are making progress in understanding the underlying biology of ALS and identifying targeted candidate therapies with diverse mechanisms of action.\n",
